.slider {
  width: 100%;
  height: 4.666666666666667rem;
  position: relative;
  text-align: center;
  overflow: hidden;
}
.slider ul {
  width: 100%;
  height: 4rem;
  overflow: hidden;
  margin: 0;
  padding: 0;
  list-style: none;
}
.slider li {
  position: absolute;
}
.slider a {
  display: block;
}
.slider img {
  width: 100%;
}
.slider .pagination {
  z-index: 2;
  line-height: 0.5;
  position: absolute;
  width: 100%;
  left: 0;
  bottom: 0.8em;
}
.slider .pagination i {
  display: inline-block;
  width: 6px;
  height: 6px;
  overflow: hidden;
  border-radius: 100%;
  background: #fff;
  opacity: 0.3;
  margin: 0 4px;
  text-indent: -9999px;
}
.slider .pagination i.on {
  background-color: #000;
}


